import React, { createContext, useState, useContext, ReactNode, useEffect } from 'react';
import { 
  DeliveryMethod, 
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  MenuItem, 
  UserAddress, 
  CartItem, 
  Order
} from '../data/adminDatabase';

// Импортируем типы из новых типов
import { City, Restaurant, FoodItem } from '../types/apiTypes';
import { api, getUserProfile, saveUserProfile } from '../services/api';

// Тип данных для состояния приложения
interface AppState {
  isAuthenticated: boolean;
  userRole: string | null;
  selectedCity: City | null;
  selectedRestaurant: number | null;
  isDarkMode: boolean;
  cities: City[];
  restaurants: Restaurant[];
  menuItems: FoodItem[];
  deliveryMethod: DeliveryMethod;
  cart: CartItem[];
  orderHistory: Order[];
}

// Тип данных для контекста приложения
interface AppContextType extends AppState {
  setIsAuthenticated: (isAuthenticated: boolean) => void;
  setUserRole: (role: string | null) => void;
  setSelectedCity: (city: City | null) => void;
  setSelectedRestaurant: (restaurantId: number | null) => Promise<void>;
  toggleDarkMode: () => void;
  setDeliveryMethod: (method: DeliveryMethod) => void;
  addToCart: (item: CartItem) => void;
  updateCartItemQuantity: (itemId: number, quantity: number) => void;
  removeFromCart: (itemId: number) => void;
  clearCart: () => void;
  addToOrderHistory: (order: Order) => void;
  placeOrder: () => Promise<Order | null>;
  updateUserAddress: (address: UserAddress) => void;
  resetUserAddressForm: () => void;
  userAddress: UserAddress | null;
  refreshRestaurantData: (restaurantId: number) => Promise<void>;
}

// Создаем контекст с начальным значением undefined
const AppContext = createContext<AppContextType | undefined>(undefined);

// Хук для использования контекста
export const useAppContext = (): AppContextType => {
  const context = useContext(AppContext);
  if (context === undefined) {
    throw new Error('useAppContext must be used within an AppProvider');
  }
  return context;
};

// Пропсы для провайдера
interface AppProviderProps {
  children: ReactNode;
}

export const AppProvider: React.FC<AppProviderProps> = ({ children }) => {
  // Начальное состояние
  const getInitialState = (): AppState => {
    return {
      isAuthenticated: localStorage.getItem('isAuthenticated') === 'true',
      userRole: localStorage.getItem('userRole'),
      selectedCity: localStorage.getItem('selectedCity') 
        ? JSON.parse(localStorage.getItem('selectedCity') || '{}') 
        : null,
      selectedRestaurant: null, // Не загружаем selectedRestaurant из localStorage при запуске
      isDarkMode: localStorage.getItem('isDarkMode') === 'true',
      cities: [],
      restaurants: [],
      menuItems: [],
      deliveryMethod: 'delivery',
      cart: [],
      orderHistory: []
    };
  };

  const [state, setState] = useState<AppState>(getInitialState);

  // Загрузка начальных данных
  useEffect(() => {
    const loadInitialData = async () => {
      try {
        const cities = await api.getCities();
        setState(prevState => ({
          ...prevState,
          cities
        }));
        
        // Если выбран город, загружаем рестораны
        if (state.selectedCity) {
          const restaurants = await api.getRestaurantsByCity(state.selectedCity.id);
          setState(prevState => ({
            ...prevState,
            restaurants
          }));
        }
        
        // Если выбран ресторан, загружаем меню
        if (state.selectedRestaurant) {
          const menuItems = await api.getMenuItems(state.selectedRestaurant);
          setState(prevState => ({
            ...prevState,
            menuItems
          }));
        }
      } catch (error) {
        console.error("Failed to load initial data:", error);
      }
    };
    
    loadInitialData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [state.selectedCity?.id, state.selectedRestaurant]);

  // Начальный адрес пользователя
  const [userAddress, setUserAddress] = useState<UserAddress | null>(null);
  
  // Загружаем профиль пользователя при инициализации
  useEffect(() => {
    // Пытаемся получить сохраненный профиль пользователя
    const savedProfile = getUserProfile();
    
    if (savedProfile && savedProfile.name) {
      console.log("Loading saved user profile:", savedProfile);
      
      // Инициализируем userAddress с данными из профиля
      // Обеспечиваем все обязательные поля для UserAddress
      setUserAddress({
        name: savedProfile.name,
        phone: savedProfile.phone || '',
        address: '', // Предоставляем пустую строку вместо undefined
        city: 0, // Предоставляем значение по умолчанию для city
      });
    }
  }, []);

  const setIsAuthenticated = (isAuthenticated: boolean) => {
    localStorage.setItem('isAuthenticated', String(isAuthenticated));
    setState(prevState => ({ ...prevState, isAuthenticated }));
  };

  const setUserRole = (role: string | null) => {
    if (role) {
      localStorage.setItem('userRole', role);
    } else {
      localStorage.removeItem('userRole');
    }
    setState(prevState => ({ ...prevState, userRole: role }));
  };

  const setSelectedCity = (city: City | null) => {
    if (city) {
      localStorage.setItem('selectedCity', JSON.stringify(city));
    } else {
      localStorage.removeItem('selectedCity');
    }
    setState(prevState => ({ ...prevState, selectedCity: city }));
    
    // Загружаем рестораны для выбранного города
    if (city) {
      api.getRestaurantsByCity(city.id)
        .then(restaurants => {
          setState(prevState => ({ ...prevState, restaurants }));
        })
        .catch(error => {
          console.error(`Error loading restaurants for city ${city.id}:`, error);
        });
    }
  };

  const setSelectedRestaurant = async (restaurantId: number | null) => {
    if (restaurantId !== null) {
      localStorage.setItem('selectedRestaurant', String(restaurantId));
      
      // Обновляем состояние сразу, чтобы пользовательский интерфейс реагировал быстрее
      setState(prevState => ({ ...prevState, selectedRestaurant: restaurantId }));
      
      try {
        // Загружаем свежие данные ресторана
        const restaurantData = await api.getRestaurantById(restaurantId);
        console.log('Получены свежие данные ресторана:', restaurantData);
        
        if (restaurantData) {
          // Преобразуем данные в правильный формат
          const formattedRestaurant: Restaurant = {
            id: restaurantData.id,
            name: restaurantData.name,
            cityId: typeof restaurantData.city === 'number' ? restaurantData.city : state.selectedCity?.id || 0,
            address: restaurantData.address || '',
            description: restaurantData.description || '',
            coverImage: restaurantData.cover_image || '',
            rating: restaurantData.rating || 0,
          };
          
          // Обновляем ресторан в списке ресторанов
          setState(prevState => ({
            ...prevState,
            restaurants: prevState.restaurants.map(r => 
              r.id === restaurantId ? formattedRestaurant : r
            )
          }));
        }
        
        // Загружаем меню для выбранного ресторана
        const menuItems = await api.getMenuItems(restaurantId);
        setState(prevState => ({ ...prevState, menuItems }));
        
      } catch (error) {
        console.error(`Ошибка при загрузке данных ресторана ${restaurantId}:`, error);
      }
      
    } else {
      localStorage.removeItem('selectedRestaurant');
      setState(prevState => ({ ...prevState, selectedRestaurant: null }));
    }
  };

  const toggleDarkMode = () => {
    const newDarkModeState = !state.isDarkMode;
    localStorage.setItem('isDarkMode', String(newDarkModeState));
    
    // Применяем или удаляем класс темной темы к body
    if (newDarkModeState) {
      document.body.classList.add('dark-theme');
    } else {
      document.body.classList.remove('dark-theme');
    }
    
    setState(prevState => ({ ...prevState, isDarkMode: newDarkModeState }));
  };

  // Функция для изменения метода доставки
  const setDeliveryMethod = (method: DeliveryMethod) => {
    setState(prevState => ({ ...prevState, deliveryMethod: method }));
  };

  // Функция для добавления товара в корзину
  const addToCart = (item: CartItem) => {
    setState(prevState => {
      // Проверяем, есть ли уже такой товар в корзине
      const existingItemIndex = prevState.cart.findIndex(cartItem => cartItem.id === item.id);
      
      if (existingItemIndex >= 0) {
        // Если товар уже есть, увеличиваем количество на 1
        const updatedCart = [...prevState.cart];
        updatedCart[existingItemIndex].quantity += 1;
        return { ...prevState, cart: updatedCart };
      } else {
        // Если товара нет, добавляем его в корзину
        return { ...prevState, cart: [...prevState.cart, item] };
      }
    });
  };

  // Функция для обновления количества товара в корзине
  const updateCartItemQuantity = (itemId: number, quantity: number) => {
    setState(prevState => {
      const updatedCart = prevState.cart.map(item => 
        item.id === itemId ? { ...item, quantity } : item
      );
      return { ...prevState, cart: updatedCart };
    });
  };

  // Функция для удаления товара из корзины
  const removeFromCart = (itemId: number) => {
    setState(prevState => ({
      ...prevState,
      cart: prevState.cart.filter(item => item.id !== itemId)
    }));
  };

  // Функция для очистки корзины
  const clearCart = () => {
    setState(prevState => ({ ...prevState, cart: [] }));
  };

  // Функция для добавления заказа в историю
  const addToOrderHistory = (order: Order) => {
    setState(prevState => ({
      ...prevState,
      orderHistory: [...prevState.orderHistory, order]
    }));
  };

  // Функция для обновления адреса пользователя
  const updateUserAddress = (address: UserAddress) => {
    setUserAddress(address);
    
    // Сохраняем имя и телефон в профиль только если они не пустые
    if (address && address.name && address.name.trim() !== '') {
      saveUserProfile({
        name: address.name,
        phone: address.phone || ''
      });
      console.log("Updated user profile with new address info");
    }
  };

  // Функция для сброса формы адреса пользователя
  const resetUserAddressForm = () => {
    // Не сбрасываем профиль полностью, но создаем новую сессию заказа
    if (typeof window !== 'undefined') {
      localStorage.setItem('orderFormSession', JSON.stringify({
        timestamp: new Date().toISOString()
      }));
    }
    console.log("User address form reset for new order");
  };

  // Функция для обновления данных ресторана
  const refreshRestaurantData = async (restaurantId: number) => {
    try {
      // Если ресторан существует в списке, обновляем его данные
      if (state.restaurants.some(r => r.id === restaurantId)) {
        const updatedRestaurant = await api.getRestaurantById(restaurantId);
        
        if (updatedRestaurant) {
          // Преобразуем данные в правильный формат для state.restaurants
          const formattedRestaurant: Restaurant = {
            id: updatedRestaurant.id,
            name: updatedRestaurant.name,
            cityId: typeof updatedRestaurant.city === 'number' ? updatedRestaurant.city : state.selectedCity?.id || 0,
            address: updatedRestaurant.address || '',
            description: updatedRestaurant.description || '',
            coverImage: updatedRestaurant.cover_image || '',
            rating: updatedRestaurant.rating || 0,
          };
          
          console.log('Получены обновленные данные ресторана:', updatedRestaurant);
          console.log('Преобразованы в формат для UI:', formattedRestaurant);
          
          setState(prevState => ({
            ...prevState,
            restaurants: prevState.restaurants.map(r => 
              r.id === restaurantId ? formattedRestaurant : r
            )
          }));
          
          console.log('Обновлены данные ресторана:', formattedRestaurant);
        }
      }
    } catch (error) {
      console.error(`Ошибка при обновлении данных ресторана (ID: ${restaurantId}):`, error);
    }
  };

  // Функция для размещения заказа
  const placeOrder = async () => {
    console.log("AppContext - placeOrder called");
    
    // Проверяем наличие товаров в корзине
    if (state.cart.length === 0) {
      console.error("Cannot place order with empty cart");
      return null;
    }
    
    try {
      // Нормализуем адрес пользователя
      let normalizedAddress: (UserAddress | undefined) = undefined;
      if (userAddress) {
        // Если city не является числом, то пытаемся использовать ID города из selectedCity
        const cityId = typeof userAddress.city === 'number' 
          ? userAddress.city 
          : (state.selectedCity ? state.selectedCity.id : 1); // Используем 1 как дефолтный ID города
        
        normalizedAddress = {
          ...userAddress,
          city: cityId
        };
      }
      
      // Создаем новый заказ и отправляем его на сервер
      const orderData: Omit<Order, 'id' | 'date' | 'status'> = {
        items: [...state.cart],
        totalAmount: state.cart.reduce((total, item) => total + item.price * item.quantity, 0),
        deliveryMethod: state.deliveryMethod,
        restaurantId: state.selectedRestaurant || 0,
        userAddress: normalizedAddress
      };
      
      console.log('Creating new order with API:', orderData);
      console.log('User address details:', normalizedAddress);
      
      // Убедимся, что адрес и имя пользователя правильно передаются
      if (normalizedAddress && normalizedAddress.name) {
        console.log('Customer name for order:', normalizedAddress.name);
      } else {
        console.warn('No customer name available in the address!');
      }
      
      const result = await api.createOrder(orderData);
      
      if (result.error) {
        console.error("Error creating order:", result.message, result);
        return null;
      }
      
      console.log('Order created successfully:', result);
      
      // Если заказ создан успешно, добавляем его в историю заказов и очищаем корзину
      if (result.data && result.data.id) {
        const newOrder: Order = {
          id: result.data.id,
          items: [...state.cart],
          totalAmount: orderData.totalAmount,
          deliveryMethod: orderData.deliveryMethod,
          date: new Date().toISOString(),
          status: 'pending',
          restaurantId: orderData.restaurantId
        };
        
        // Добавляем заказ в историю
        addToOrderHistory(newOrder);
        
        // Очищаем корзину
        clearCart();
        
        // Сбрасываем форму адреса для следующего заказа
        resetUserAddressForm();
        
        return newOrder;
      }
      
      return null;
    } catch (error) {
      console.error("Error in placeOrder:", error);
      return null;
    }
  };

  // Эффект для синхронизации изменений меню из админ-панели
  useEffect(() => {
    // Обработчик обновления меню
    const handleMenuUpdate = (e: CustomEvent) => {
      // Проверяем, относится ли обновление к выбранному ресторану
      if (state.selectedRestaurant) {
        const updatedMenu = e.detail.menuItems;
        if (Array.isArray(updatedMenu)) {
          // Обновляем состояние с новым меню
          setState(prevState => ({
            ...prevState,
            menuItems: updatedMenu
          }));
          console.log('Menu synchronized with admin changes:', updatedMenu.length);
        }
      }
    };

    // Добавляем слушатель события
    window.addEventListener('menuItemsUpdated', handleMenuUpdate as EventListener);

    // Очищаем слушатель при размонтировании
    return () => {
      window.removeEventListener('menuItemsUpdated', handleMenuUpdate as EventListener);
    };
  }, [state.selectedRestaurant]);

  // Применяем тему при инициализации
  React.useEffect(() => {
    if (state.isDarkMode) {
      document.body.classList.add('dark-theme');
    } else {
      document.body.classList.remove('dark-theme');
    }
  }, [state.isDarkMode]);

  return (
    <AppContext.Provider
      value={{
        ...state,
        setIsAuthenticated,
        setUserRole,
        setSelectedCity,
        setSelectedRestaurant,
        toggleDarkMode,
        setDeliveryMethod,
        addToCart,
        updateCartItemQuantity,
        removeFromCart,
        clearCart,
        addToOrderHistory,
        placeOrder,
        updateUserAddress,
        resetUserAddressForm,
        userAddress,
        refreshRestaurantData
      }}
    >
      {children}
    </AppContext.Provider>
  );
};

export default AppContext; 