// Добавляем типизацию для window.API_CONFIG
declare global {
  interface Window {
    API_CONFIG?: {
      API_BASE_URL: string;
    };
  }
}

// Базовый URL API - используем переменные окружения с fallback
const API_BASE_URL = window.API_CONFIG?.API_BASE_URL || process.env.REACT_APP_API_URL || 'http://localhost:8000/api';

// Добавляем диагностическую информацию
console.log('API Service initialized with:', {
  API_BASE_URL,
  window_location: window.location.href,
  hasConfigObject: !!window.API_CONFIG
});

// Глобальные настройки для всех fetch-запросов
const createFetchOptions = (options: RequestInit = {}): RequestInit => {
  const defaultOptions: RequestInit = {
    credentials: 'include',
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json; charset=utf-8',
      // Добавляем заголовки для мобильных устройств
      'X-Requested-With': 'XMLHttpRequest',
    },
    // Важно для мобильных устройств и Telegram WebApp
    mode: 'cors',
    ...options
  };

  // Объединяем пользовательские заголовки с дефолтными
  if (options.headers) {
    defaultOptions.headers = {
      ...defaultOptions.headers,
      ...options.headers
    };
  }

  return defaultOptions;
};

// Типы для результата запроса
interface ApiResponse<T = any> {
  data: T | null;
  error: { message: string } | null;
  status: number;
}

// Текстовый ответ API
interface TextApiResponse {
  message: string;
}

// Универсальная функция для выполнения запросов
const fetchWithRetry = async <T = any>(url: string, options: RequestInit = {}, retries = 1): Promise<ApiResponse<T>> => {
  try {
    // Добавляем базовые настройки ко всем запросам
    const fetchOptions = createFetchOptions(options);
    
    // Логируем все запросы для отладки
    console.log(`[API Request] ${options.method || 'GET'} ${url}`, { options: fetchOptions });
    
    // Добавляем таймаут для запроса
    const controller = new AbortController();
    const timeoutId = setTimeout(() => controller.abort(), 15000); // 15-секундный таймаут
    
    const startTime = Date.now();
    const response = await fetch(url, {
      ...fetchOptions,
      signal: controller.signal
    });
    const requestTime = Date.now() - startTime;
    
    clearTimeout(timeoutId);
    
    // Логируем все ответы
    console.log(`[API Response] ${response.status} ${url} (${requestTime}ms)`, {
      ok: response.ok,
      status: response.status,
      statusText: response.statusText,
      headers: Object.fromEntries(Array.from(response.headers.entries()))
    });
    
    // Если запрос успешен, возвращаем результат
    if (response.ok) {
      const contentType = response.headers.get('content-type');
      
      // Проверяем, что ответ - JSON
      if (contentType && contentType.includes('application/json')) {
        const data = await response.json();
        return { data, error: null, status: response.status };
      } else {
        // Если это не JSON, возвращаем текст
        const text = await response.text();
        try {
          // Попытка распарсить как JSON, даже если сервер вернул неправильный Content-Type
          const jsonData = JSON.parse(text);
          console.log(`[API Warning] Response has content-type "${contentType}" but contains valid JSON`);
          return { data: jsonData as T, error: null, status: response.status };
        } catch (e) {
          // Возвращаем текст как message с правильной типизацией
          console.log(`[API Warning] Non-JSON response from server: ${text.substring(0, 100)}...`);
          return { data: { message: text } as unknown as T, error: null, status: response.status };
        }
      }
    }
    
    // Если 401/403, возможно, проблема с авторизацией
    if (response.status === 401 || response.status === 403) {
      console.error(`Auth error: ${response.status}`);
      // Обрабатываем случай с неавторизованным доступом
      return { 
        data: null, 
        error: { message: 'Authentication required' }, 
        status: response.status 
      };
    }
    
    // Для других ошибок пытаемся получить сообщение об ошибке
    let errorMessage;
    try {
      const errorData = await response.json();
      errorMessage = errorData.detail || errorData.message || 'Unknown error';
    } catch (e) {
      errorMessage = await response.text() || `HTTP error: ${response.status}`;
    }
    
    return { 
      data: null, 
      error: { message: errorMessage }, 
      status: response.status 
    };
    
  } catch (error: any) { // Указываем тип any для переменной error
    // Обработка сетевых ошибок и ошибок запроса
    console.error(`[API Error] Network error for ${url}: ${error.message || 'Unknown error'}`);
    
    // Если это ошибка превышения времени ожидания (timeout)
    if (error.name === 'AbortError') {
      console.error(`[API Timeout] Request timeout for ${url}`);
      return { 
        data: null, 
        error: { message: 'Request timed out' }, 
        status: 0 
      };
    }
    
    // Если есть попытки для повтора запроса
    if (retries > 0) {
      console.log(`[API Retry] Retrying ${url}... ${retries} attempts left`);
      
      // Ждем перед повторной попыткой (увеличивая время с каждой попыткой)
      await new Promise(resolve => setTimeout(resolve, (2 - retries) * 1000));
      
      // Рекурсивно повторяем запрос
      return fetchWithRetry(url, options, retries - 1);
    }
    
    // Если больше нет попыток, возвращаем ошибку
    return { 
      data: null, 
      error: { message: error.message || 'Network error' }, 
      status: 0 
    };
  }
};

// Типы для кэширования
type CacheKey = string;
type CacheValue = any;
type CacheTimeStamp = number;

// Типы данных, соответствующие бэкенду
export interface City {
  id: number;
  name: string;
}

export interface Restaurant {
  id: number;
  name: string;
  city: number;
  address: string;
  description: string;
  cover_image: string;
  rating: number;
}

// Простая система кэширования
class CacheService {
  private cache: Map<CacheKey, { value: CacheValue; timestamp: CacheTimeStamp }> = new Map();
  private readonly defaultExpiration = 5 * 60 * 1000; // 5 минут

  set(key: CacheKey, value: CacheValue, expiration: number = this.defaultExpiration): void {
    this.cache.set(key, {
      value,
      timestamp: Date.now() + expiration,
    });
  }

  get(key: CacheKey): CacheValue | null {
    const cached = this.cache.get(key);
    
    if (!cached) {
      return null;
    }

    if (Date.now() > cached.timestamp) {
      this.cache.delete(key);
      return null;
    }

    return cached.value;
  }

  invalidate(key: CacheKey): void {
    this.cache.delete(key);
  }

  invalidateAll(): void {
    this.cache.clear();
  }
}

const cacheService = new CacheService();

// Функция обработки ошибок API
const handleApiError = (error: any, message: string) => {
  // Улучшенное логирование ошибок
  if (error.response) {
    console.error(`${message}:`, {
      status: error.response.status,
      data: error.response.data,
      message: error.message
    });
  } else if (error instanceof Error) {
    console.error(`${message}:`, error.message);
  } else {
    console.error(`${message}:`, error);
  }
  
  // Возвращаем структурированный объект ошибки вместо простого выброса исключения
  return {
    error: true,
    message: error.message || message,
    status: error.response?.status || 500,
    data: error.response?.data || null
  };
};

// Функция для исправления кодировки русских символов
function fixRussianEncoding(text: string): string {
  // Таблица соответствия неправильно закодированных символов
  const replacements: Record<string, string> = {
    'Ð': 'А', 'Ñ': 'Б', 'Ò': 'В', 'Ã': 'Г', 'Ä': 'Д', 'Å': 'Е', 'Æ': 'Ж',
    'Ç': 'З', 'È': 'И', 'É': 'Й', 'Ê': 'К', 'Ë': 'Л', 'Ì': 'М', 'Í': 'Н',
    'Î': 'О', 'Ï': 'П', 'Р': 'Р', 'С': 'С', 'Т': 'Т', 'Ó': 'У', 'Ô': 'Ф',
    'Õ': 'Х', 'Ö': 'Ц', 'Ч': 'Ч', 'Ø': 'Ш', 'Ù': 'Щ', 'Ú': 'Ъ', 'Û': 'Ы',
    'Ь': 'Ь', 'Ý': 'Э', 'Þ': 'Ю', 'ß': 'Я',
    'à': 'а', 'á': 'б', 'â': 'в', 'ã': 'г', 'ä': 'д', 'å': 'е', 'æ': 'ж',
    'ç': 'з', 'è': 'и', 'é': 'й', 'ê': 'к', 'ë': 'л', 'ì': 'м', 'í': 'н',
    'î': 'о', 'ï': 'п', 'ð': 'р', 'ñ': 'с', 'ò': 'т', 'ó': 'у', 'ô': 'ф',
    'õ': 'х', 'ö': 'ц', 'ч': 'ч', 'ø': 'ш', 'ù': 'щ', 'ú': 'ъ', 'û': 'ы',
    'ь': 'ь', 'ý': 'э', 'þ': 'ю', 'ÿ': 'я',
    'Ð¹': 'й', 'Ð°': 'а', 'Ð±': 'б', 'Ð²': 'в', 'Ð³': 'г', 'Ð´': 'д', 'Ðµ': 'е',
    'Ð¶': 'ж', 'Ð·': 'з', 'Ð¸': 'и', 'Ð¹й': 'й', 'Ðº': 'к', 'Ð»': 'л', 'Ð¼': 'м',
    'Ð½': 'н', 'Ð¾': 'о', 'Ð¿': 'п', 'Ñ€': 'р', 'Ñс': 'с', 'Ñ‚': 'т', 'Ñƒ': 'у',
    'Ñ„': 'ф', 'Ñ…': 'х', 'Ñ†': 'ц', 'Ñ‡': 'ч', 'Ñˆ': 'ш', 'Ñ‰': 'щ', 'ÑŠ': 'ъ',
    'Ñ‹': 'ы', 'ÑŒ': 'ь', 'Ñэ': 'э', 'ÑŽ': 'ю', 'Ñя': 'я'
  };
  
  // Заменяем символы по таблице
  return text.replace(/[Ð-ß]|Ñ.|Ð.|Ñ‚|Ñƒ|Ñ„|Ñ…|Ñ†|Ñ‡|Ñˆ|Ñ‰|ÑŠ|Ñ‹|ÑŒ|Ñ|ÑŽ|Ñ/g, (match) => {
    return replacements[match] || match;
  });
}

// API сервис с поддержкой кэширования
export const api = {
  // Получение списка городов
  async getCities() {
    const cacheKey = 'cities';
    const cachedData = cacheService.get(cacheKey);
    
    if (cachedData) {
      return cachedData;
    }
    
    try {
      const response = await fetch(`${API_BASE_URL}/cities/`);
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      const data = await response.json();
      
      cacheService.set(cacheKey, data);
      return data;
    } catch (error) {
      return handleApiError(error, 'Error fetching cities');
    }
  },

  // Получение ресторанов по городу
  async getRestaurantsByCity(cityId: number) {
    const cacheKey = `restaurants_city_${cityId}`;
    const cachedData = cacheService.get(cacheKey);
    
    if (cachedData) {
      return cachedData;
    }
    
    try {
      const response = await fetch(`${API_BASE_URL}/cafes/?city=${cityId}`);
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      const data = await response.json();
      
      cacheService.set(cacheKey, data);
      return data;
    } catch (error) {
      return handleApiError(error, `Error fetching restaurants for city ${cityId}`);
    }
  },

  // Получение ресторана по ID
  async getRestaurantById(restaurantId: number) {
    const cacheKey = `restaurant_${restaurantId}`;
    
    // Инвалидируем кэш для этого ресторана, чтобы всегда получать свежие данные
    cacheService.invalidate(cacheKey);
    
    try {
      const response = await fetch(`${API_BASE_URL}/cafes/${restaurantId}/`, {
        credentials: 'include',
        headers: {
          'Accept': 'application/json',
        }
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const data = await response.json();
      
      // Трансформируем данные в формат, ожидаемый фронтендом
      const transformedData = {
        id: data.id,
        name: data.name,
        city: data.city,
        address: data.address || '',
        description: data.description || '',
        cover_image: data.cover_image || '',
        rating: data.rating || 0
      };
      
      return transformedData;
    } catch (error) {
      console.error(`Error fetching restaurant with ID ${restaurantId}:`, error);
      return null;
    }
  },

  // Получение меню ресторана
  async getMenuItems(restaurantId?: number) {
    const cacheKey = `menu_items_${restaurantId || 'all'}`;
    const cachedData = cacheService.get(cacheKey);
    
    if (cachedData) {
      console.log(`[DEBUG] Using cached menu data for restaurant ID ${restaurantId}`);
      return cachedData;
    }
    
    try {
      const url = restaurantId 
        ? `${API_BASE_URL}/menu-items/?cafe=${restaurantId}`
        : `${API_BASE_URL}/menu-items/`;
      
      console.log(`[DEBUG] Fetching menu from URL: ${url}`);
      
      const response = await fetch(url, {
        credentials: 'include',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json; charset=utf-8'
        }
      });
      
      console.log(`[DEBUG] API response status: ${response.status}, OK: ${response.ok}`);
      
      if (!response.ok) {
        const errorText = await response.text();
        console.error(`[DEBUG] API error response: ${errorText}`);
        throw new Error(`HTTP error! status: ${response.status}, message: ${errorText}`);
      }
      
      const rawText = await response.text();
      console.log(`[DEBUG] Raw API response (first 100 chars): ${rawText.substring(0, 100)}...`);
      
      // Проверка и исправление кодировки при необходимости
      let processedText = rawText;
      if (rawText.includes('Ð') || rawText.includes('Ñ')) {
        console.log('[DEBUG] Detected encoding issues, attempting to fix...');
        processedText = fixRussianEncoding(rawText);
        console.log(`[DEBUG] Fixed text (first 100 chars): ${processedText.substring(0, 100)}...`);
      }
      
      // Парсим JSON после исправления
      let data;
      try {
        data = JSON.parse(processedText);
      } catch (parseError) {
        console.error(`[DEBUG] JSON parse error after encoding fix:`, parseError);
        // Пробуем исходный текст как запасной вариант
        try {
          data = JSON.parse(rawText);
          console.log('[DEBUG] Parsed using original text instead');
        } catch (secondError) {
          console.error(`[DEBUG] Both parsing attempts failed:`, secondError);
          throw parseError;
        }
      }
      
      console.log(`[DEBUG] Parsed data type: ${typeof data}, isArray: ${Array.isArray(data)}, length: ${Array.isArray(data) ? data.length : 'N/A'}`);
      
      if (!Array.isArray(data)) {
        console.error('[DEBUG] Unexpected API response format', data);
        throw new Error('Unexpected API response format, expected array');
      }
      
      // Проверяем каждый элемент на корректность
      const sampleItem = data.length > 0 ? data[0] : null;
      console.log(`[DEBUG] Sample menu item:`, sampleItem);
      
      // Исправляем кодировку в каждом элементе если нужно
      const fixedData = data.map((item: any) => {
        if (typeof item.name === 'string' && (item.name.includes('Ð') || item.name.includes('Ñ'))) {
          console.log(`[DEBUG] Fixing encoding for item: ${item.name}`);
          return {
            ...item,
            name: fixRussianEncoding(item.name),
            description: typeof item.description === 'string' ? fixRussianEncoding(item.description) : item.description
          };
        }
        return item;
      });
      
      // Трансформируем данные в формат, ожидаемый фронтендом
      const transformedData = fixedData.map((item: any) => {
        const transformedItem = {
          id: item.id,
          restaurantId: item.cafe,
          name: item.name,
          description: item.description || '',
          price: item.price,
          category: item.category,
          imageUrl: item.image_url || item.image || '',
          available: item.available !== undefined ? item.available : true
        };
        
        return transformedItem;
      });
      
      console.log(`[DEBUG] Transformed ${transformedData.length} menu items`);
      if (transformedData.length > 0) {
        console.log(`[DEBUG] First transformed item:`, transformedData[0]);
      }
      
      cacheService.set(cacheKey, transformedData);
      return transformedData;
    } catch (error) {
      console.error(`[DEBUG] getMenuItems error:`, error);
      return handleApiError(error, 'Error fetching menu items');
    }
  },

  // Создание заказа
  async createOrder(order: Omit<Order, 'id' | 'date' | 'status'>) {
    try {
      // Проверка корректности данных
      if (!order.restaurantId) {
        return {
          error: true,
          message: 'Restaurant ID is required',
          status: 400,
          data: null
        };
      }
      
      if (!order.items || order.items.length === 0) {
        return {
          error: true,
          message: 'Order must contain at least one item',
          status: 400,
          data: null
        };
      }
      
      // Получаем ID текущего пользователя из localStorage, если есть
      let userId = null;
      try {
        const storedUser = localStorage.getItem('currentUser');
        if (storedUser) {
          const user = JSON.parse(storedUser);
          userId = user.id || null;
        }
      } catch (e) {
        console.warn('Failed to get user ID from localStorage', e);
      }
      
      // Если у нас выбрана доставка, но нет ID адреса, нужно создать адрес
      let deliveryAddressId = null;
      if (order.deliveryMethod === 'delivery' && order.userAddress) {
        // Пытаемся создать адрес, если его нет
        try {
          console.log('Creating delivery address:', order.userAddress);
          
          // Проверяем есть ли у адреса id. Если нет, создаем новый
          if (!order.userAddress.id) {
            const addressResponse = await this.addUserAddress({
              name: order.userAddress.name,
              phone: order.userAddress.phone,
              address: order.userAddress.address,
              city: order.userAddress.city
            });
            
            if (addressResponse && addressResponse.id) {
              deliveryAddressId = addressResponse.id;
              console.log('Successfully created delivery address with ID:', deliveryAddressId);
            } else {
              console.warn('Failed to create delivery address');
            }
          } else {
            deliveryAddressId = order.userAddress.id;
          }
        } catch (e) {
          console.error('Error creating delivery address:', e);
        }
      }
      
      // Рассчитываем общую сумму заказа
      const totalPrice = order.items.reduce((total, item) => {
        return total + (item.price * item.quantity);
      }, 0);
      
      // Проверяем, что имя клиента задано и не пустое
      const customerName = order.userAddress?.name;
      console.log('Customer name for order:', customerName);
      
      // Сбросим текущую сессию заказа для избежания перезаписи данных
      clearCurrentSessionProfile();
      
      // Сохраняем информацию о пользователе в профиль только если имя не пустое
      if (customerName && customerName.trim() !== '') {
        // Сохраняем имя и телефон в локальное хранилище для будущих заказов
        saveUserProfile({
          name: customerName,
          phone: order.userAddress?.phone || ''
        });
        console.log('Saved customer profile data:', { name: customerName, phone: order.userAddress?.phone });
      }
      
      // Преобразование данных в формат, ожидаемый API
      const orderData = {
        cafe: order.restaurantId,
        order_type: order.deliveryMethod === 'delivery' ? 'delivery' : 'in_place',
        delivery_address: deliveryAddressId,
        status: 'new', // Используем 'new' вместо 'pending', совместимый с бэкендом
        user: userId,
        total_price: totalPrice,
        customer: customerName || 'Гость', // Приоритет имени клиента
        items_data: order.items.map(item => ({
          menu_item: item.id,
          quantity: item.quantity
        }))
      };

      console.log('Sending order data to API:', orderData);

      const response = await fetch(`${API_BASE_URL}/orders/`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body: JSON.stringify(orderData),
        credentials: 'include'
      });

      const responseData = await response.text();
      console.log('API response text:', responseData);
      
      let data;
      
      try {
        // Пытаемся распарсить ответ как JSON
        data = responseData ? JSON.parse(responseData) : {};
        console.log('Parsed API response:', data);
      } catch (e) {
        console.error('Failed to parse response as JSON:', responseData);
        data = { message: responseData };
      }
      
      if (!response.ok) {
        console.error('API returned error:', response.status, data);
        return {
          error: true,
          message: data.detail || `Failed to create order: ${response.status}`,
          status: response.status,
          data: null
        };
      }
      
      // После успешного создания заказа, дополнительно отправляем запрос на создание элементов заказа,
      // если они не были созданы автоматически
      if (data && data.id && (!data.items || data.items.length === 0)) {
        console.log(`Order ${data.id} created, but no items attached. Adding items...`);
        
        try {
          // Проверяем существуют ли элементы заказа
          const itemsCheckResponse = await fetch(`${API_BASE_URL}/order_items/?order=${data.id}`, {
            credentials: 'include'
          });
          
          if (itemsCheckResponse.ok) {
            const existingItems = await itemsCheckResponse.json();
            
            if (!existingItems || existingItems.length === 0) {
              console.log('No existing order items found, creating them...');
              
              // Создаем элементы заказа вручную
              for (const item of order.items) {
                const orderItemData = {
                  order: data.id,
                  menu_item: item.id,
                  quantity: item.quantity
                };
                
                const itemResponse = await fetch(`${API_BASE_URL}/order_items/`, {
                  method: 'POST',
                  headers: {
                    'Content-Type': 'application/json',
                  },
                  body: JSON.stringify(orderItemData),
                  credentials: 'include'
                });
                
                if (!itemResponse.ok) {
                  console.error(`Failed to create order item: ${item.name}`);
                }
              }
            } else {
              console.log(`Found ${existingItems.length} existing order items`);
            }
          }
        } catch (e) {
          console.error('Error checking/creating order items:', e);
        }
      }
      
      // Инвалидируем все связанные кэши для получения свежих данных
      cacheService.invalidate('orders');
      cacheService.invalidate('user_orders');
      cacheService.invalidate(`cafe_${order.restaurantId}_stats`);
      cacheService.invalidate(`restaurant_${order.restaurantId}`);
      cacheService.invalidate(`restaurant_${order.restaurantId}_orders`);
      
      // Важно также инвалидировать кэши статистики
      cacheService.invalidate('admin_statistics');
      cacheService.invalidate(`statistics_${order.restaurantId}`);
      
      console.log('Order created successfully, returning data:', data);
      
      // Возвращаем успешный результат
      return {
        error: false,
        message: 'Order created successfully',
        status: response.status,
        data
      };
    } catch (error) {
      console.error('Error in createOrder:', error);
      return {
        error: true,
        message: error instanceof Error ? error.message : 'Unknown error occurred',
        status: 500,
        data: null
      };
    }
  },

  // Получение заказов пользователя
  async getUserOrders(forceRefresh = false) {
    const cacheKey = 'user_orders';
    
    // Получаем сохраненный профиль пользователя
    const userProfile = getUserProfile();
    
    // Проверяем кэш, если разрешено
    const cachedData = cacheService.get(cacheKey);
    if (cachedData && !forceRefresh) {
      console.log('Returning cached user orders');
      return cachedData;
    }
    
    try {
      const response = await fetch(`${API_BASE_URL}/orders/`, {
        credentials: 'include'
      });
      
      if (!response.ok) {
        throw new Error(`Error fetching orders: ${response.status}`);
      }
      
      const data = await response.json();
      console.log('Fetched user orders:', data);
      
      if (!Array.isArray(data)) {
        console.error('Unexpected response format:', data);
        return [];
      }
      
      // Преобразуем данные в формат, ожидаемый фронтендом
      const transformedOrders = await Promise.all(data.map(async (order: any) => {
        // Запрашиваем детальную информацию о заказе через специальный метод API
        try {
          console.log(`Fetching detailed info for order ${order.id}`);
          const orderDetailsResponse = await fetch(`${API_BASE_URL}/orders/${order.id}/details/`, {
            credentials: 'include'
          });
          
          if (!orderDetailsResponse.ok) {
            console.error(`Error fetching order details: ${orderDetailsResponse.status}`);
            throw new Error(`Error fetching order details`);
          }
          
          const orderDetails = await orderDetailsResponse.json();
          console.log(`Received details for order ${order.id}:`, orderDetails);
          
          // Получаем имя клиента прямо из заказа, а не из профиля, чтобы избежать смешивания
          let customerName = orderDetails.customer_name || orderDetails.customer || order.customer;
          
          // Используем имя из профиля только если нет имени клиента в заказе
          if (!customerName || customerName === 'Гость' || customerName === 'Anonymous') {
            if (userProfile?.name) {
              customerName = userProfile.name;
              console.log(`No customer name in order, using profile name: ${customerName}`);
            } else {
              customerName = 'Неизвестный покупатель';
            }
          }
          
          console.log(`Order ${order.id} customer name resolved to: ${customerName}`);
          
          // Преобразуем элементы заказа в формат CartItem
          const items = orderDetails.items_detailed ? 
            orderDetails.items_detailed.map((item: any) => ({
              id: item.menu_item_id,
              restaurantId: order.cafe,
              name: item.name,
              description: item.description || '',
              price: item.price,
              category: '', // Категория может отсутствовать в деталях заказа
              imageUrl: item.image_url || '',
              quantity: item.quantity
            })) : [];
          
          return {
            id: order.id,
            items,
            totalAmount: order.total_price,
            deliveryMethod: order.order_type === 'delivery' ? 'delivery' : 'pickup',
            date: order.created_at,
            status: order.status,
            restaurantId: order.cafe,
            userAddress: order.delivery_address,
            customerName: customerName
          };
        } catch (detailsError) {
          console.error(`Failed to fetch details for order ${order.id}:`, detailsError);
          
          // Улучшаем обработку ошибки - пытаемся получить имя из всех возможных источников
          let customerName = order.customer;
          
          // Если имя клиента "Гость" или пусто, используем имя из профиля пользователя
          if ((!customerName || customerName === 'Гость' || customerName === 'Anonymous') && userProfile?.name) {
            customerName = userProfile.name;
            console.log(`Using profile name as fallback: ${customerName}`);
          }
          
          if (!customerName) {
            customerName = 'Неизвестный покупатель';
          }
          
          console.log(`Using fallback customer name for order ${order.id}: ${customerName}`);
          
          // Если не удалось получить детали, используем основную информацию
          return {
            id: order.id,
            items: [], // Пустой массив, так как нет информации о товарах
            totalAmount: order.total_price,
            deliveryMethod: order.order_type === 'delivery' ? 'delivery' : 'pickup',
            date: order.created_at,
            status: order.status,
            restaurantId: order.cafe,
            userAddress: order.delivery_address,
            customerName: customerName
          };
        }
      }));
      
      console.log('Transformed orders for frontend:', transformedOrders);
      cacheService.set(cacheKey, transformedOrders);
      return transformedOrders;
    } catch (error) {
      console.error('Error in getUserOrders:', error);
      return handleApiError(error, 'Error fetching user orders');
    }
  },

  // Обновление статуса заказа
  async updateOrderStatus(orderId: number, status: Order['status']) {
    try {
      // Преобразуем статус в формат бэкенда
      const backendStatus = status;
      
      const response = await fetch(`${API_BASE_URL}/orders/${orderId}/update_status/`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ status: backendStatus }),
        credentials: 'include'
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const data = await response.json();
      
      // Инвалидируем кэш заказов
      cacheService.invalidate('user_orders');
      cacheService.invalidate(`order_${orderId}`);
      
      return { orderId, status: data.status };
    } catch (error) {
      return handleApiError(error, `Error updating order ${orderId} status`);
    }
  },

  // Добавление адреса пользователя
  async addUserAddress(address: Omit<UserAddress, 'id'>) {
    try {
      // Подготавливаем данные для отправки на бэкенд
      const userData: {
        name: string;
        phone: string;
        address: string;
        city: any;
        user?: number;
      } = {
        name: address.name,
        phone: address.phone,
        address: address.address,
        // Проверяем тип поля city для правильной отправки
        city: typeof address.city === 'object' && address.city !== null && 'id' in address.city 
          ? address.city.id 
          : address.city
      };
      
      // Если не указан пользователь, добавляем ID из localStorage (если есть)
      try {
        const storedUser = localStorage.getItem('currentUser');
        if (storedUser) {
          const user = JSON.parse(storedUser);
          if (user.id) {
            userData.user = user.id;
          }
        }
      } catch (e) {
        console.warn('Failed to get user ID for address:', e);
      }
      
      console.log('Creating user address with data:', userData);
      
      const response = await fetch(`${API_BASE_URL}/addresses/`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(userData),
        credentials: 'include'
      });
      
      if (!response.ok) {
        const errorText = await response.text();
        console.error(`Error adding user address: ${response.status}`, errorText);
        throw new Error(`HTTP error! status: ${response.status}, message: ${errorText}`);
      }
      
      const data = await response.json();
      console.log('Address created successfully:', data);
      
      // Инвалидируем кэш адресов
      cacheService.invalidate('user_addresses');
      
      return data;
    } catch (error) {
      console.error('Error in addUserAddress:', error);
      return null;
    }
  },

  // Получение адресов пользователя
  async getUserAddresses() {
    const cacheKey = 'user_addresses';
    const cachedData = cacheService.get(cacheKey);
    
    if (cachedData) {
      return cachedData;
    }
    
    try {
      const response = await fetch(`${API_BASE_URL}/addresses/`, {
        credentials: 'include'
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const data = await response.json();
      
      cacheService.set(cacheKey, data);
      return data;
    } catch (error) {
      return handleApiError(error, 'Error fetching user addresses');
    }
  }
};

// Экспортируем сервис кэширования для возможности прямого использования
export { cacheService };

// Функция для сохранения информации о пользователе в localStorage
export const saveUserProfile = (userData: { name?: string, phone?: string }) => {
  try {
    // Получаем текущие данные пользователя или создаем новые
    const currentUserData = localStorage.getItem('userProfile') 
      ? JSON.parse(localStorage.getItem('userProfile') || '{}')
      : {};
    
    // Обновляем только предоставленные поля и только если они не пустые
    const updatedUserData = {
      ...currentUserData,
      ...Object.entries(userData).reduce((acc, [key, value]) => {
        // Сохраняем новое значение только если оно не пустое
        if (value && value.trim() !== '') {
          acc[key] = value;
        } else if (currentUserData[key]) {
          // Сохраняем текущее значение если новое пустое
          acc[key] = currentUserData[key];
        }
        return acc;
      }, {} as Record<string, string>),
      lastUpdated: new Date().toISOString()
    };
    
    // Сохраняем обновленные данные
    localStorage.setItem('userProfile', JSON.stringify(updatedUserData));
    console.log('User profile updated:', updatedUserData);
    
    return updatedUserData;
  } catch (e) {
    console.error('Error saving user profile:', e);
    return null;
  }
};

// Функция для очистки текущей сессии профиля
export const clearCurrentSessionProfile = () => {
  try {
    // Создаем временный ключ для текущей сессии заказа
    localStorage.setItem('currentOrderSession', JSON.stringify({
      timestamp: new Date().toISOString()
    }));
    
    console.log('Current order session reset');
    return true;
  } catch (e) {
    console.error('Error clearing current session profile:', e);
    return false;
  }
};

// Функция для получения сохраненного профиля пользователя
export const getUserProfile = () => {
  try {
    const profileData = localStorage.getItem('userProfile');
    if (!profileData) {
      return null;
    }
    
    // Получаем текущую сессию заказа, если она есть
    const orderSession = localStorage.getItem('orderFormSession');
    let sessionActive = false;
    
    if (orderSession) {
      try {
        // Если есть активная сессия, используем ее контекст
        sessionActive = true;
        console.log('Active order session detected');
      } catch (e) {
        console.error('Error parsing order session:', e);
      }
    }
    
    const parsedData = JSON.parse(profileData);
    console.log('Retrieved user profile data:', parsedData);
    
    // Возвращаем профиль только если нет активной сессии заказа или это намеренный запрос
    if (sessionActive) {
      console.log('Using profile within active session');
    }
    
    return parsedData;
  } catch (e) {
    console.error('Error getting user profile:', e);
    return null;
  }
}; 